.hamburger {
  display: none;
}

@media screen and (max-width: 500px) {
  .hamburger {
    width: 24px;
    height: 24px;
    display: block;
    border: none;
    background-color: transparent;
    padding: 0;
    margin: 0;
    cursor: pointer;
    outline: none;
  }
}

.hamburger:after,
.hamburger:before,
.hamburger span {
  background-color: #fff;
  content: "";
  display: block;
  height: 3px;
  margin: 5px 0;
  transition: all 0.3s ease-in-out;
}
